import { Component, OnInit, Input, Output,EventEmitter } from '@angular/core';
import { BudgetItem } from 'src/shared/models/budget-item.model';
import { MatDialog } from '@angular/material';
import { EditItemModelComponent } from '../edit-item-model/edit-item-model.component';


@Component({
  selector: 'app-budget-item-list',
  templateUrl: './budget-item-list.component.html',
  styleUrls: ['./budget-item-list.component.scss']
})




export class BudgetItemListComponent implements OnInit {
@Input() budgetItems: BudgetItem[];
@Output() delete: EventEmitter<any> = new EventEmitter<BudgetItem>();
@Output() update: EventEmitter<any> = new EventEmitter<BudgetItem>();
  constructor(public dailog:MatDialog) { }

  ngOnInit() {
  }

onDeleteButtonClicked(item: BudgetItem){
  this.delete.emit(item);

}
onCardClicked(item:BudgetItem){
const dialogRef = this.dailog.open(EditItemModelComponent,{
  width: '580px',
  data:item
});
dialogRef.afterClosed().subscribe(result => {
  if(result){
    //replace the item with the updated /submitted item from the form
   // this.budgetItems[this.budgetItems.indexOf(item)]=result;
   this.update.emit({
     old:item,
     new:result
   })
  }
})


}

}


export interface UpdateEvent {
  old : BudgetItem;
  new : BudgetItem;
}